export default {
    data() {
      return {
          form: {
          email: '',
          name: '',
          country: '',
          phone: '',
          message: '',
        },
        show: true
        };
   },
    methods: {
      showModal() {
        this.$refs['my-dialog'].show()
      },
      hideModal() {
        this.$refs['my-dialog'].hide()
      },
      onSubmit(event) {
        event.preventDefault()
        alert(JSON.stringify(this.form))
        this.onReset()
      },
      onReset(event) {
        event.preventDefault()
        // Reset our form values
        this.form.email = ''
        this.form.name = ''
        this.form.message = ''
        this.form.phone = ''
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      }
    }

}
